@import "fonts"; // Шрифты
@import "reset"; // Сброс стилей
@import "setting"; // Общие настройки
@import "vars"; // Переменные
@import "mixins"; // Миксины

// Компоненты
@import "components/footer";
@import "components/header";

// Стили

body {
  background: $bg-color;
  position: relative;
}
.header {
  position: absolute;
  top: 0;
  width: 100%;

  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 40px 0px;
  }

  .logo {
    font-weight: 400;
    font-size: 24px;
    line-height: 30px;
    color: #ffffff;
  }

  .menu {
    display: flex;
    align-items: center;
    @media (max-width: 575px) {
      display: none;
    }
    .active {
      a {
        color: $main-color;
      }
    }
    li {
      padding: 0px 20px;
      a {
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: 1px;
        text-transform: uppercase;
        color: $secondary-color;
        transition: 0.2s linear;
        &:hover {
          color: $main-color;
        }
      }
    }
  }

  .lang-swicher {
    display: flex;
    align-items: center;
    .active {
      a {
        color: $main-color;
      }
    }
    li {
      padding: 0px 5px;
      a {
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: 1px;
        text-transform: uppercase;
        color: $secondary-color;
        transition: 0.2s linear;
        &:hover {
          color: $main-color;
        }
      }
    }
  }
}

.home-screen {
  background-image: url("../img/home-bg.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  padding-top: 80px;
  &__inner {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100vh;
    max-height: 590px;
    .down-btn {
      margin-top: 70px;
      cursor: pointer;
      transition: 0.2s linear;

      &:hover {
        opacity: 0.5;
      }
    }
    .home {
      &-title {
        font-weight: 500;
        font-size: 48px;
        line-height: 53px;
        text-align: center;
        color: #ffffff;
      }

      &-subtitle {
        font-style: normal;
        font-weight: 500;
        font-size: 32px;
        line-height: 53px;
        text-align: center;
        color: #ffffff;
        @media (max-width: 575px) {
          font-size: 16px;
        }
      }
    }
  }
}

.section {
  margin-top: 100px;
  @media (max-width: 768px) {
    margin-top: 40px;
  }
  &-title {
    font-weight: 400;
    font-size: 25px;
    line-height: 30px;
    text-align: center;
    color: #ffffff;
    margin-bottom: 40px;
    @media (max-width: 768px) {
      font-size: 20px;
    }
  }
}

.about {
  &__inner {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 63px;
    @media (max-width: 992px) {
      grid-template-columns: repeat(3, 1fr);
    }

    @media (max-width: 768px) {
      grid-template-columns: repeat(2, 1fr);
      gap: 30px;
    }

    @media (max-width: 560px) {
      grid-template-columns: 250px;
      justify-content: center;
      gap: 60px;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    color: #fff;
    p {
      font-weight: 400;
      font-size: 18px;
      line-height: 24px;
      text-align: center;
      color: #ffffff;
      padding-top: 20px;
    }
  }
}

.default-btn {
  font-weight: 400;
  font-size: 13px;
  line-height: 19px;
  text-align: center;
  text-transform: uppercase;
  color: $secondary-color;
  padding: 10px 25px;
  border: 1px solid #999999;
  border-radius: 3px;
  letter-spacing: 1px;
  transition: all 0.2s linear;
  &:hover {
    cursor: pointer;
    color: $main-color;
    border-color: $main-color;
  }

  &:focus {
    border-color: $main-color;
  }

  &:active {
    opacity: 0.5;
  }
}

.default-btn.active {
  color: $main-color;
  border-color: $main-color;
}

.portfolio {
  &-tabs {
    display: flex;
    margin: 0 auto;
    width: max-content;
    @media (max-width: 580px) {
      flex-direction: column;
    }
    li button {
      width: 150px;
    }

    li {
      margin: 0px 15px;
      @media (max-width: 580px) {
        margin-bottom: 15px;
        margin-left: 0;
        margin-right: 0;
      }
    }
  }

  &__contents {
    min-height: 515px;
    &-inner {
      display: grid;
      grid-template-columns: repeat(auto-fill, minmax(280px, 300px));
      grid-gap: 20px;
      padding-top: 40px;
      justify-content: center;
      .portfolio-item {
        position: relative;
        overflow: hidden;
        border-radius: 5px;
        img {
          width: 100%;
          height: auto;
          object-fit: cover;
          border-radius: 5px;
          transition: all 0.2s linear;
        }

        .hover-panel {
          position: absolute;
          width: 100%;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          background: rgba(34, 34, 34, 0.78);
          border-radius: 5px;
          top: 0;
          left: 0;
          color: #00d6b4;
          text-transform: uppercase;
          font-weight: 400;
          font-size: 16px;
          line-height: 30px;
          text-align: center;
          text-transform: uppercase;
          opacity: 0;
          visibility: hidden;
          transition: all 0.2s linear;
        }

        &:hover {
          .hover-panel {
            visibility: visible;
            opacity: 1;
          }
          img {
            transform: scale(1.1);
          }
        }
      }
    }
    .tab-item {
      display: none;
    }

    .tab-item.active {
      display: block;
    }
  }
}

.input-default {
  border: 1px solid #999999;
  border-radius: 3px;
  font-weight: 400;
  font-size: 15px;
  line-height: 30px;
  color: #999999;
  padding: 5px 10px;
  width: 100%;
  max-height: 150px;
  background: #222222;
  margin-bottom: 15px;
  &::placeholder {
    font-weight: 400;
    font-size: 15px;
    line-height: 30px;
    color: #999999;
  }

  &:focus {
    border-color: $main-color;
  }

  &:invalid {
    &::placeholder {
      color: #ff5f5f;
    }
    border-color: #ff5f5f;
  }
}

.input-default.invalid {
  &::placeholder {
    color: #ff5f5f;
  }
  border-color: #ff5f5f;
}

.contact {
  &-form {
    max-width: 300px;
    width: 100%;
    margin: 0 auto;
    input[type="submit"] {
      width: 100%;
    }
  }
}

.footer {
  margin-top: 180px;
  padding: 40px 0px;
  text-align: center;
  span,
  a {
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    text-align: center;
    text-transform: uppercase;
    color: #c2c2c2;
    display: block;
  }

  a {
    transition: 0.2s linear;
    padding-top: 5px;
    &:hover {
      color: $main-color;
    }
  }
}
